import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import logo from '../assets/images/logo.png';
import icon1 from '../assets/images/linkedin.svg'
import icon2 from '../assets/images/github.svg'
export const NavBar =()=>{
    return(
    <Navbar expand="sm">
      <Container>
      <Navbar.Brand href="#home">
            <img
              alt=""
              src={logo}
              width="30"
              height="30"
              className="logo"
            />
          </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="#home" className={'navbar-link'} >Home</Nav.Link>
            <Nav.Link href="#skills" className={'navbar-link'} >Skills</Nav.Link>
            <Nav.Link href="#projects" className={'navbar-link'}>Projects</Nav.Link>
          </Nav>
          <span className='navbar-text'>
            <div className='social-icon'>
                <a href='https://linkedin.com/in/piyush-jain-083934221' target="_blank"><img src={icon1} alt='LinkedIn'/></a>
                <a href='https://github.com/piyushj513' target="_blank"><img src={icon2} alt='Github'/></a>
            </div><a href='#contact'>
            <button className='navbar-text'><span>Let's Connect</span></button>
            </a>
          </span>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}    