import { Container } from "react-bootstrap";
import sms from '../assets/images/sms.png';
import sudoku from '../assets/images/sudoku.png';
import weather from '../assets/images/weather.png';
import website from '../assets/images/website.png';

export const Projects = () =>{
    return(
        <section id="projects">
            <Container>
                <div className="row">
                <h2> Projects </h2>
                <p>These are some of my projects</p>
                    <div className="col-lg-3 col-md-4 col-xs-6 thumb" >
                        <a href="https://github.com/piyushj513/SalaryManagementSystem" target="_blank" className="pj">
                            <img src={sms} className="zoom img-fluid "  alt="sms"/>
                        </a>
                        <p> Salary Management System</p>
                    </div>
                    <div className="col-lg-3 col-md-4 col-xs-6 thumb" >
                        <a href="https://github.com/piyushj513/WeatherApp" target="_blank" className="pj">
                            <img src={weather} className="zoom img-fluid "  alt="sms"/>
                        </a>
                        <p>Live Weather App</p>
                    </div>
                    <div className="col-lg-3 col-md-4 col-xs-6 thumb" >
                        <a href="https://github.com/piyushj513/SudokuSolver" target="_blank" className="pj">
                            <img src={sudoku} className="zoom img-fluid "  alt="sms"/>
                        </a>
                        <p>Java Sudoku Solver</p>
                    </div>
                    <div className="col-lg-3 col-md-4 col-xs-6 thumb" >
                        <a href="#" className="pj" target="_blank">
                            <img src={website} className="zoom img-fluid " alt="sms"/>
                        </a>
                        <p>Portfolio Website</p>
                    </div>
                </div>
            </Container>
        </section>
    )
}
     